$blue: #2d3e50;
$turquoise: #01999a;
$mint: #19bc9d;
$gray: #f1f5e6;

/*  global */
@import url("https://fonts.googleapis.com/css?family=El+Messiri&display=swap");
body {
  font-family: "El Messiri", sans-serif, arial;
  background-color: #fff;
  font-size: 14px;
  direction: rtl;
}
ul,
ol {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

p.h4,
p.h5 {
  line-height: 1.8;
}
.bg-blue {
  background-color: $blue;
}
.bg-mint {
  background-color: $mint;
}
.bg-gray {
  background-color: $gray;
}

.top-button {
  border-radius: 5px;
  padding: 5px;
  position: fixed;
  left: 15px;
  bottom: 10%;
  display: none;
  transition: background-color 0.3s ease-in;
  &:hover {
    cursor: pointer;
    background-color: $mint;
  }
}

/*  //global */

/* Headre */
.lesson-header {
  .navbar-nav--custom {
    background-color: $turquoise;
    .nav-item {
      transition: background-color 0.3s ease-in-out;

      &.active {
        background-color: #fff;
        .nav-link {
          color: $blue;
        }
      }
      &:hover {
        background-color: #fff;
        .nav-link {
          color: $blue;
          transition: color 0.3s ease;
        }
      }
      .nav-link {
        color: #fff;
      }
    }
  }
}
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: $mint;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: $blue;
}
/* // Header */
/* carousel */
.carousel-indicators {
  .carousel-indicators__li--custom {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 2px solid #fff;
    opacity: 1;
    background-color: transparent;
    &.active {
      background-color: #fff;
      transition: background-color 0.4s ease-in-out;
    }
  }
}
/* carousel */

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  body {
    padding-top: 75px;
  }
  p.h4,
  p.h5 {
    line-height: 1.6;
    font-size: 1rem;
    text-align: center;
  }

  img.float-right,
  img.float-left {
    float: none !important;
    display: block;
    width: 100% !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    margin-bottom: 15px;
  }

  .lesson-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;

    #lesson-title {
      font-size: 1.1rem;
      text-align: right;
    }
    .navbar-toggler--custom {
      background-color: $mint;
      position: absolute;
      top: 15px;
      left: 15px;
    }
  }
  footer {
    .footer-content {
      font-size: 0.7rem;
    }
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 576px) and (max-width: 991.98px) {
  body {
    padding-top: 75px;
  }
  p.h4,
  p.h5 {
    line-height: 1.6;
    font-size: 1rem;
    text-align: center;
  }

  img.float-right,
  img.float-left {
    float: none !important;
    display: block;
    width: 100% !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    margin-bottom: 15px;
  }

  .lesson-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;

    #lesson-title {
      font-size: 1.1rem;
      text-align: right;
    }
    .navbar-toggler--custom {
      background-color: $mint;
      position: absolute;
      top: 15px;
      left: 15px;
    }
  }
}
